.ads-desktop {
    text-align: center;
    @media only screen and (max-width: 720px) {
        display: none;
    }
}

.ads-mobile {
    text-align: center;
    @media only screen and (min-width: 720px) {
        display: none;
    }
}

.ads-section {
    text-align: center;
    margin-bottom: 20px;
}

.-with-side-ads {
    display: flex;
    .ads-side-section {
        margin: 0px 30px;
        @media only screen and (max-width: 930px) {
            display: none;
        }
    }

    
    .main-content {
        flex-grow: 1;
    }

    // .mock-ads {
